import { CircleUserRound } from 'lucide-react'
import React from 'react'

const signUpPage = () => {
  return (
    <>
    <div className='mt-16 w-[100%]'>
     <div>
      <form>
      <CircleUserRound />
        <input type="text" placeholder='Name'  />
        <input type="email" name="email" placeholder='E-mail' />
      </form>
     </div>
    </div>
    </>
  )
}

export default signUpPage
